.widgetLg {
    flex: 2;

    -webkit-box-shadow: 0px 0px 12px -1px #000000;
    box-shadow: 0px 0px 12px -1px #000000;
    padding: 20px;
}

.widgetLgTitle {
    font-size: 22px;
    font-weight: 600;
}

.widgetLgTable {
    width: 100%;
    border-spacing: 20px;
}

.widgetLgTh {
    text-align: left;
}

.widgetLgTd {
    width: 40px;
    height: 40px;
    font-weight: 300;
}

.widgetLgName {
    display: flex;
    align-items: center;
    font-weight: 600;
    margin-right: 10px;
}


.widgetLgButton {
    padding: 5px 7px;
    border : none;
    border-radius: 10px;
}

.widgetLgButton.배송완료 {
    background-color: #e5faf2;
    color: #3bb077;
}

.widgetLgButton.배송중 {
    background-color: #e0effa;
    color: #3266f3;
}


